import React, { Component } from 'react';
import { connect } from 'react-redux';
import { app } from '../../config/app';
import Grid from '../../utility/grid';
import Switchtoggle from '../../components/form/switchtoggle';
import Table from '../../components/grid/Table';
import NoRecord from '../../components/grid/NoRecord';
import { FONT_COLORS, FONT, FONT_DELETE, FONT_STATUS_UPDATE } from "../../utility/constants";
import Form from '../../utility/Form';
import { save } from '../../actions/fontcolor';
import Forms, { initialState, validation } from "./form";
import toastr from 'toastr';

class Fontcolor extends Component {

    constructor(props) {
        super(props);
        this.state = {
            openPopup : false,
            isEdit : false
        }
        this.displayRows = this.displayRows.bind(this);
        this.state = initialState();
        const formValidation = validation();
        this.grid = Grid.getInstance({ currentComponent: this, url: 'getListsWithPagination', actionType: FONT_COLORS, method: 'POST' });

        this.font = Form.getInstance(
            this,formValidation,
            (fields) => {
                this.props.dispatch(save(fields));
                this.resetForm();
            },
            {},
            {}
        );   
    }
    initialState() {
        return {
            filters: {
                domain_id: this.props.app.DOMAIN_ID,
                searchVal: '',
                modelName: 'promize_font_color',
                searchKey: 'font_color_name',
                idKey: 'promize_font_color_id'
            },
            fields :{
                font_color_name : '',
                font_color_code : '',
                domain_id : app.DOMAIN_ID,
                status : 1
            },
            fonts: [],
            inputErrors:{},
        }
    }

    componentDidMount() {
        this.grid.getRecords();
    }
    resetForm(){
        let fields = {
            font_color_name : '',
            font_color_code : '',
            domain_id : app.DOMAIN_ID,
            status : 1
        }
        this.setState({fields},()=>{
            this.grid.getRecords();
        })
    }

    setData(props) {
        const response = props.fontcolors.response;
        const fontResponse = props.fontcolor.response;
        if (response !== this.props.fontcolors.response) {
            if (!props.fontcolors.isFetching &&  response.data && Object.keys(response.data).length > 0 && response.status && response.status === 200) {
                this.setState({ fonts: response.data || [], openPopup : false });
            }
            else if (!props.fontcolors.isFetching && props.fontcolors.isError) {
                this.setState({ fonts: [] });
            }
        }
        if (fontResponse !== this.props.fontcolor.response) {
            if (!props.fontcolor.isFetching && !props.fontSize.isError   && props.fontcolor.response.data && Object.keys(props.fontcolor.response.data).length > 0 && props.fontcolor.response.status && props.fontcolor.response.status === 200) {
                toastr.success(props.fontcolor.response.message)
                this.setState({ openPopup : false });
            }
            else if (!props.fontcolor.isFetching && props.fontcolor.isError) {
                toastr.error(props.fontcolor.response.message)
            }
        }
    }
    editFont(e, font){
        e.preventDefault();
        this.setState({fields : font,openPopup : !this.state.openPopup, isEdit : true});
    }
    displayRows() {
        const fonts = this.state.fonts;
        let fontRows = <NoRecord isFetching={this.props.fontcolors.isFetching} colspan="10" />;
        if (fonts.length > 0) {
            fontRows = fonts.map((font, index) => {
                var status = (font.status == 1) ? true : false;
                //var clipart_category_image = clipart.
                return <tr key={index}>
                    <td>{font.font_color_name}</td>
                    <td>{font.font_color_code}</td>
                    <td>
                        <Switchtoggle  {...this.props} 
                            value={status}
                            fieldName={'status'}
                            id={font.promize_font_color_id}
                            onChange={(e) => { this.grid.statusUpdate({ url: "changeStatus", actionType: FONT_STATUS_UPDATE, params: font, modelName: "promize_font_color",
                            PKey: "promize_font_color_id" }, e) }}
                            spanLabel={{ first: 'Active', second: 'InActive' }}
                            checkValue={{ first: true, second: false }}
                        />
                    </td>
                    <td>
                        <div className="table-actions">
                            <button className="btn-tbl edit" onClick={(e) => this.editFont(e,font)}><i className="fa fa-pencil-alt"></i></button>
                            <button className="btn-tbl delete" onClick={(e) => this.table.delete.handleToogleModal({ id: font.promize_font_color_id, idKey: 'ids', url: 'deleteRecord', actionType: FONT_DELETE, modelName: "promize_font_color", PKey: "promize_font_color_id" }, e)}><i className="fa fa-trash-alt"></i></button>
                        </div>
                    </td>
                </tr>
            })
        }
        return fontRows;
    }
    componentWillReceiveProps(nextProps) {
        this.setData(nextProps);
    }
    handleChangeComplete = (color) => {
        let field = this.state.fields;
        let obj = { ...field, font_color_code: color.hex}
        this.setState({ fields: obj });
    };
    enablePopup = () =>{
        let fields = {
            font_color_name : '',
            font_color_code : '',
            domain_id : app.DOMAIN_ID,
            status : 1
        }
        this.setState({fields, openPopup : !this.state.openPopup, isEdit : false})
    }
    render() {
        return (
            <React.Fragment>
                  {this.state.openPopup && <div>
                       <Forms {...this} font={this.font} handleChangeComplete={this.handleChangeComplete} title={this.state.isEdit ? "Edit Font Color" : "Add Font Color"} enablePopup={this.enablePopup} currentComponent = {this}/>
                    </div>
                    }
            <Table
                ref={instance => { this.table = instance; }}
                {...this.props} {...this.state}
                pageTitle="Font Color"
                managementType={FONT_COLORS}
                addButton={
                    <button className="btn btn-promize-primary" onClick={this.enablePopup}>
                        <span>{"Add"}</span>
                    </button>
                }
                grid={this.grid}
                data={this.props.fontcolors}
                rowDelete={this.props.fontDelete}
            >
                <table className="promize-table table-hover table-action" id="fixTable">
                    <thead>
                        <tr>
                            <th className={(this.grid.sortType) ? ((this.grid.sortType == 'ASC') ? "sorting-asec" : "sorting-desc") : "sorting"} onClick={(e) => this.grid.handleColSorting(e)}>Name</th>
                            <th>Code</th>
                            <th>Status</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.displayRows()}
                    </tbody>
                </table>
            </Table>
            </React.Fragment>)

    }
}

function mapStateToProps(state) {
    return {
        fontStatus: state.fontStatus,
        fontDelete: state.fontDelete,
        fileUpload : state.fileUpload,
        fontcolors: state.fontcolors,
        fontcolor: state.fontcolor,
    };
}

export default connect(mapStateToProps)(Fontcolor);
