import React from 'react';
import { SketchPicker } from 'react-color';
import reactCSS from 'reactcss'
import colorPicker from '../../assets/images/color-picker.png';

class InputColor extends React.Component {
	state = {
		color: {
			r: '241',
			g: '112',
			b: '19',
			a: '1',
		},
		displayColorPicker: false
	}

	handleClick = e => {
	//	e.preventDefault()
		this.setState({ displayColorPicker: !this.state.displayColorPicker })
	}
	handleColorChange = color => {
		this.setState({ color: color.rgb })
		this.props.handleChangeComplete(color)
	}
	classNameCheck() {
		const validation = this.props.form.validation(this.props.fieldName)
		const message = validation ? this.props.state.inputErrors[this.props.fieldName].message : '';
		const classValidation = `form-group ${validation ? 'feild-wrong' : ''} ${this.props.value && !validation ? 'feild-sucess' : ''}`
		const className = this.props.validation ? classValidation : 'form-group';
		return { 'className': className, 'message': message, 'validation': validation };
	}

	input() {
		const validation = this.classNameCheck();
		const styles = reactCSS({
			'default': {
				color: {
					width: '36px',
					height: '14px',
					borderRadius: '2px',
					background: `rgba(${this.state.color.r}, ${this.state.color.g}, ${this.state.color.b}, ${this.state.color.a})`,
				},
				swatch: {
					padding: '4px 6px 0px',
					background: '#fff',
					borderRadius: '1px',
					display: 'inline-block',
					cursor: 'pointer',
				},
				popover: {
					position: 'absolute',
					zIndex: '2',
					bottom: '40px',
					right: '0',
				},
				cover: {
					position: 'fixed',
					top: '0px',
					right: '0px',
					bottom: '70px',
					left: '0px',
				},
			},
		});
		return (<React.Fragment><div className="promize-form-group promize-form-addon">
			<div className="promize-picker-control">
				<label htmlFor={this.props.fieldName} className="promize-form-control-label">{this.props.labelName}{this.props.validation && <sup>*</sup>}</label>
				<input className="promize-form-control"
					type={this.props.inputType ? this.props.inputType : "text"}
					id={this.props.fieldName}
					step={this.props.inputStep ? this.props.inputStep : ""}
					data-type={this.props.dataType || "text"}
					data-name={this.props.dataName}
					placeholder={this.props.placeholder ? this.props.placeholder : ""}
					name={this.props.fieldName}
					onBlur={this.props.onBlur || this.props.form.blurEventListner}
					disabled={this.props.disabled}
					onChange={this.props.onChange || this.props.form.handleFieldsChange}
					value={this.props.value}
					data-validation-name={this.props.labelName}
					maxLength={this.props.maxLength} />
			</div>
			<div className="promize-picker">
				<div style={styles.swatch} onClick={this.handleClick}>
					<img src={colorPicker}/>
					{/* <div style={styles.color} /> */}
				</div>
				{this.state.displayColorPicker && <div style={styles.popover}>
					<div style={styles.cover} onClick={this.handleClick} />
					<SketchPicker
						disableAlpha={true}
						color={this.props.state.fields.font_color_code}
						onChangeComplete={this.handleColorChange} />
				</div>
				}
			</div>
			{validation['validation'] && <span className="promize-form-error">{`${this.props.labelName} ` + validation['message']}</span>}
		</div></React.Fragment >);
	}

	render() {

		return (<React.Fragment>
			{this.input()}
		</React.Fragment>);
	}
}

export default InputColor;
