import { FONT_COLOR,FONT_COLORS,PENDING, FULFILLED, REJECTED } from "../../utility/constants";
import { app } from "../../config/app";
export function save(data) {
    let method = 'POST', url = "createRecord";
    data = {
        ...data,
        modelName: "promize_font_color",
        idKey: "promize_font_color_id"
    }
    if (data.promize_font_color_id) {
        method = 'PUT';
        url = 'updateRecord'
        delete data.created_date;
        delete data.updated_date;
    }
    return {
        types: [`${FONT_COLOR}_${PENDING}`, `${FONT_COLOR}_${FULFILLED}`, `${FONT_COLOR}_${REJECTED}`],
        method: method,
        url: url,
        data: data,
        payload: { receiveType: 'postData' }
    }
}

export function getAllFontColors() {
    let data = {
        modelName: "promize_font_color",
        idKey: "promize_font_color_id",
        domain_id: app.DOMAIN_ID
    }
    const request = {
        types: [`${FONT_COLORS}_${PENDING}`, `${FONT_COLORS}_${FULFILLED}`, `${FONT_COLORS}_${REJECTED}`],
        method: 'POST',
        url: 'getAllRecords',
        data: data,
    }

    return request;
}
